export default{
	data(){
		return{
			left:100,
			Height:0,
			data_one:['促销','分期','仅看有货'],
			data_two:['耳机','户外','配件'],
			
			index_1:null,
			index_2:null,
		}
	},
	created() {
		this.Height = uni.getSystemInfoSync().windowWidth*(~~(750*uni.getSystemInfoSync().windowHeight/uni.getSystemInfoSync().windowWidth)-176)/750
		
	},
	mounted() {
		this.left = 0
	},
	methods:{
		indexSele_one(index){
			this.index_1 = index
		},
		indexSele_two(index){
			this.index_2 = index
		},
		vanish(){
			this.left = 100
			setTimeout(()=>{
				this.$emit('vanish')
			},500)
		},
		confirm(){
			this.vanish()
		},
		reset(){
			this.index_1 = null
			this.index_2 = null
		}
	}
}
